import React, { Component } from 'react'
import { Table, Radio } from 'antd'
import { connect } from 'dva'
const RadioGroup = Radio.Group;
const RadioButton = Radio.Button;
let mapState = store => {
  return { ...store.showUser }
}
@connect(mapState)
class ShowUser extends Component {
  state = {
    nowData: 0
  }
  createUserColumns = () => {
    return [
      {
        title: '用户名',
        dataIndex: 'user_name',
        key: 'user_name'
      },
      {
        title: '密码',
        dataIndex: 'user_pwd',
        key: 'user_pwd'
      }, {
        title: '身份',
        dataIndex: 'identity_text',
        key: 'identity_text',
        render(identity_text) {
          return unescape(identity_text)
        }
      }
    ]
  }
  transforUser = (user) => {
    return user.map((item, key) => {
      return { ...item, key: item.user_id }
    })
  }
  transforIdentity = (identity) => {
    return identity.map(item => {
      return { ...item, key: item.identity_id }
    })
  }
  transforApiAuthority = (api_authority) => {
    return api_authority.map(item => {
      return { ...item, key: item.api_authority_id }
    })
  }
  transforIdentityApi = (identity_api_relation) => {
    return identity_api_relation.map(item => {
      return { ...item, key: item.identity_api_authority_relation_id }
    })
  }
  transforViewAuthority = (view_authority) => {
    return view_authority.map(item=>{
      return { ...item, key: item.view_authority_id }
    })
  }
  transforIdentityView = (identity_view_relation) => {
    return identity_view_relation.map(item => {
      return { ...item, key: item.identity_view_authority_relation_id }
    })
  }
  createApiAuthorityColumns = () => {
    return [
      {
        title: 'api权限名称',
        dataIndex: 'api_authority_text',
        key: 'api_authority_text',
        render(api_authority_text) {
          return unescape(api_authority_text);
        }
      },
      {
        title: 'api权限url',
        dataIndex: 'api_authority_url',
        key: 'api_authority_url',
      },
      {
        title: 'api权限方法',
        dataIndex: 'api_authority_method',
        key: 'api_authority_method'
      }
    ]
  }
  createIdentityColumns = () => {
    return [
      {
        key: 'identity_text',
        dataIndex: 'identity_text',
        title: '身份名称',
        render(identity_text) {
          return unescape(identity_text);
        }
      }
    ]
  }
  createIdentityApiColumns = () => {
    return [
      {
        key: 'identity_text',
        dataIndex: 'identity_text',
        title: '身份名称'
      },
      {
        key: 'api_authority_text',
        dataIndex: 'api_authority_text',
        title: 'api权限名称'
      },
      {
        key: 'api_authority_url',
        dataIndex: 'api_authority_url',
        title: 'api权限url'
      },
      {
        key: 'api_authority_method',
        dataIndex: 'api_authority_method',
        title: 'api权限方法'
      }
    ]
  }
  createViewAuthorityColumns = () => {
    return [
      {
        dataIndex:'view_authority_text',
        key:'view_authority_text',
        title:'视图权限名称'
      },
      {
        dataIndex:'view_id',
        key:'view_id',
        title:'视图id'
      }
    ]
  }
  createIdentityViewColumns = () => {
    return [
      {
        dataIndex:'identity_text',
        key:'identity_text',
        title:'身份'
        ,
      },
      {
        dataIndex:'view_authority_text',
        key:'view_authority_text',
        title:'视图名称'
      },
      {
        dataIndex:'view_id',
        key:'view_id',
        title:'视图id'
      }
    ]
  }

  componentDidMount() {
    this.props.dispatch({ type: 'showUser/requestUser' });
    this.props.dispatch({ type: 'showUser/requestIdentity' });
    this.props.dispatch({ type: 'showUser/requestApiAuthority' });
    this.props.dispatch({ type: 'showUser/requestIdentityApi' });
    this.props.dispatch({ type: 'showUser/requestViewAuthority' });
    this.props.dispatch({ type: 'showUser/requestIdentityView' });
  }
  render() {
    window.aab = this;
    let {
      user,
      identity,
      api_authority,
      identity_api_authority_relatioin,
      identity_view_authority_relatioin,
      view_authority
    } = this.props;
    let { nowData } = this.state;
    return <div>
      <RadioGroup defaultValue={0} onChange={e => this.setState({ nowData: e.target.value })}>
        <RadioButton value={0}>用户数据</RadioButton>
        <RadioButton value={1}>身份数据</RadioButton>
        <RadioButton value={2}>api接口权限</RadioButton>
        <RadioButton value={3}>身份和api接口关系</RadioButton>
        <RadioButton value={4}>视图接口权限</RadioButton>
        <RadioButton value={5}>身份和视图权限关系</RadioButton>
      </RadioGroup>
      {
        nowData === 0 ? <React.Fragment>
          <h1>用户数据</h1>
          <Table dataSource={this.transforUser(user)} columns={this.createUserColumns()}></Table>
        </React.Fragment> : null
      }

      {
        nowData === 1 ? <React.Fragment>
          <h1>身份数据</h1>
          <Table dataSource={this.transforIdentity(identity)} columns={this.createIdentityColumns()} />
        </React.Fragment> : null
      }
      {
        nowData === 2 ? <React.Fragment>
          <h1>api接口权限</h1>
          <Table dataSource={this.transforApiAuthority(api_authority)} columns={this.createApiAuthorityColumns()} />
        </React.Fragment> : null
      }
      {
        nowData === 3 ? <React.Fragment>
          <h1>身份和api接口关系</h1>
          <Table dataSource={this.transforIdentityApi(identity_api_authority_relatioin)} columns={this.createIdentityApiColumns()} />
        </React.Fragment> : null
      }
      {
        nowData === 4 ? <React.Fragment>
          <h1>视图接口权限</h1>
          <Table dataSource={this.transforViewAuthority(view_authority)} columns={this.createViewAuthorityColumns()} />
        </React.Fragment> : null
      }
      {
        nowData === 5 ? <React.Fragment>
          <h1>身份和视图权限关系</h1>
          <Table dataSource={this.transforIdentityView(identity_view_authority_relatioin)} columns={this.createIdentityViewColumns()} />
        </React.Fragment> : null
      }
    </div>
  }
}
export default ShowUser;
